/* -- ОБЩИЕ -- */
*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

body {
  background-color: #ffffff;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #101613;
  letter-spacing: normal; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0; }

.nowrap {
  white-space: nowrap; }

/* -- CONTAINER -- */
.container {
  max-width: 1366px;
  padding: 0 98px;
  margin: 0 auto;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .container {
      max-width: 768px;
      padding: 0 45px;
      margin: 0 auto; } }
  @media (max-width: 767px) {
    .container {
      max-width: 320px;
      padding: 0 30px;
      margin: 0 auto; } }

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 0;
  overflow-y: auto;
  visibility: hidden;
  background-color: #ffffff;
  -webkit-overflow-scrolling: touch; }
  .modal--active {
    visibility: visible; }

/* -- HEADER -- */
.header {
  position: relative;
  min-height: 176px; }

.header__top {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 176px;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  .header__top--brown {
    background-color: #a87b62; }
  @media (max-width: 1023px) {
    .header__top {
      height: 126px; } }
  @media (max-width: 767px) {
    .header__top {
      height: 92px; } }

.header__burger {
  position: absolute;
  left: 45px;
  top: 55px;
  z-index: 100;
  /* -- MOBILE -- */ }
  @media (max-width: 767px) {
    .header__burger {
      left: 15px;
      top: 38px; } }

.header__logo {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  fill: #a87b62;
  /* -- MOBILE -- */ }
  .header__logo--white {
    fill: #ffffff; }
  @media (max-width: 767px) {
    .header__logo {
      top: 20px;
      width: 52px;
      height: 52px; } }

.header__logo svg {
  /* -- MOBILE -- */ }
  @media (max-width: 767px) {
    .header__logo svg {
      width: 52px;
      height: 52px; } }

.header__cart {
  position: absolute;
  right: 45px;
  top: 55px;
  z-index: 100;
  height: 18px;
  width: 20px;
  border-style: none;
  cursor: pointer;
  background-repeat: no-repeat;
  background-image: url("../img/cart.svg");
  background-color: #ffffff;
  transition: 0.3s;
  /* -- MOBILE -- */ }
  @media (min-width: 1024px) {
    .header__cart {
      display: none; } }
  .header__cart:hover {
    opacity: 0.7; }
  .header__cart:active {
    opacity: 0.5; }
  .header__cart--white {
    background-image: url("../img/cart_white.svg");
    background-color: #a87b62; }
  @media (max-width: 767px) {
    .header__cart {
      right: 15px;
      top: 38px; } }

.header__nav {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .header__nav--fullscreen {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 5;
      flex-wrap: nowrap;
      flex-direction: column;
      justify-content: flex-start;
      width: 100%;
      height: 100vh;
      padding-top: 136px;
      padding-right: 45px;
      padding-left: 45px;
      background-color: #a87b62;
      overflow-y: auto; }
    .header__nav--hidden {
      display: none; } }
  @media (max-width: 767px) {
    .header__nav {
      padding-top: 102px;
      padding-right: 15px;
      padding-left: 15px; } }

.header__search-form {
  position: relative;
  width: 50%;
  padding-top: 43px;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  .header__search-form::before {
    position: absolute;
    bottom: 5px;
    left: 0;
    width: 14px;
    height: 14px;
    content: "";
    background-image: url("../img/search.svg"); }
  @media (max-width: 1023px) {
    .header__search-form {
      width: 100%;
      padding-top: 0;
      margin-bottom: 45px; }
      .header__search-form::before {
        bottom: 12px;
        left: 12px;
        width: 16px;
        height: 16px;
        background-image: url("../img/search_white.svg"); } }
  @media (max-width: 767px) {
    .header__search-form {
      margin-bottom: 35px; } }

.header__search-form input {
  padding-left: 30px;
  font-size: 14px;
  line-height: 21px;
  border: none;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__search-form input {
      width: 100%;
      height: 40px;
      padding-left: 50px;
      background-color: rgba(255, 255, 255, 0.15);
      border-radius: 21px; }
      .header__search-form input::placeholder {
        color: transparent; } }

.header__user-menu {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  width: 50%;
  padding: 0;
  padding-top: 43px;
  margin: 0;
  margin-bottom: 51px;
  font-size: 14px;
  line-height: 21px;
  list-style: none;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__user-menu {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      padding-top: 0;
      padding-bottom: 0;
      order: 4; } }

.header__user-menu li {
  margin-left: 35px;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__user-menu li {
      margin-left: 0; }
      .header__user-menu li:last-child {
        display: none; } }

.header__user-menu a {
  color: #101613;
  transition: 0.3s;
  /* -- TABLET -- */ }
  .header__user-menu a:hover {
    opacity: 0.7; }
  .header__user-menu a:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .header__user-menu a {
      font-size: 18px;
      line-height: 130%;
      color: #ffffff; } }

.header__menu {
  width: 100%;
  border-top: 1px solid #f6f5f4;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .header__menu {
      order: 2;
      margin-bottom: 42px;
      border-top: none; } }
  @media (max-width: 767px) {
    .header__menu {
      margin-bottom: 32px; } }

.header__menu ul {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 61px;
  padding: 0;
  margin: 0;
  margin-left: -20px;
  list-style: none;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__menu ul {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      margin-left: 0; } }

.header__menu li {
  margin-left: 35px;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__menu li {
      margin-left: 0; }
      .header__menu li:not(:last-child) {
        margin-bottom: 21px; } }

.header__menu a {
  color: #101613;
  transition: 0.3s;
  /* -- TABLET -- */ }
  .header__menu a:hover {
    opacity: 0.7; }
  .header__menu a:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .header__menu a {
      font-size: 24px;
      line-height: 36px;
      color: #ffffff; } }

.header__about-menu {
  display: none;
  padding: 0;
  margin: 0;
  list-style: none;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .header__about-menu {
      display: block;
      margin-bottom: 42px;
      order: 3; } }
  @media (max-width: 767px) {
    .header__about-menu {
      margin-bottom: 32px; } }

.header__about-menu li:not(:last-child) {
  margin-bottom: 21px; }

.header__about-menu a {
  font-size: 18px;
  line-height: 130%;
  color: rgba(255, 255, 255, 0.75); }

/* -- HEADER -- */
.burger {
  height: 18px;
  width: 25px;
  border-style: none;
  cursor: pointer;
  background-repeat: no-repeat;
  background-image: url("../img/burger.svg");
  transition: 0.3s; }
  @media (min-width: 1024px) {
    .burger {
      display: none; } }
  .burger:hover {
    opacity: 0.7; }
  .burger:active {
    opacity: 0.5; }
  .burger--white {
    background-image: url("../img/burger_white.svg");
    background-color: #a87b62; }

/* -- LOG IN -- */
.login__inner {
  width: 430px;
  height: 586px;
  padding: 160px 45px 45px 45px;
  background: #f0eae4 url("../img/login_bg_1x.jpg") no-repeat left top;
  background-size: 430px 138px;
  box-shadow: 0 5px 32px rgba(226, 221, 215, 0.24); }

.login h2 {
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 130%;
  text-align: center; }

.login input[type="email"],
.login input[type="password"] {
  width: 100%;
  border: none;
  border-bottom: 1px solid #1d1613;
  font-size: 14px;
  line-height: 21px;
  color: #9b9997;
  background-color: transparent; }
